const infixFunction = (operator) => {
  switch (operator) {
    case '&': return (left, right) => left && right;
    case '<': return (left, right) => left < right;
    case '>': return (left, right) => left > right;
    case '>=': return (left, right) => left >= right;
    case '<=': return (left, right) => left <= right;
    case '!=': return (left, right) => left !== right;
    case '=': return (left, right) => left === right;
  }
}

module.exports = {
  infixFunction,
  helpers: {
    simpleFunc: val => val > 10,
    otherFunc: val => val + '-yep-',
    prctTruc: (propName, data) => data.prctThing[propName],
    prctSpecie: () => true,
    mention: () => true,
    specie: () => 'todo'
  }
}



